import jQuery from 'jquery';
import isOnScreen from './is-on-screen';

jQuery( function( $ ) {
	const stretchContainer = () => {
		const t = $( 'body' );
		$( '.gutenify-container-stretch' ).each( function() {
			const r = $( this ),
				i = r.data( 'stretch-type' ),
				n = 'full-stretched-padded' === i ? '' : 0,
				wrapperDiv = $( this ).children();
			wrapperDiv.css( {
				'margin-left': 0,
				'margin-right': 0,
				'padding-left': n,
				'padding-right': n,
			} );
			const l = r.offset().left - t.offset().left,
				a = t.outerWidth() - l - r.parent().outerWidth();
			wrapperDiv.css( {
				'margin-left': -l + 'px',
				'margin-right': -a + 'px',
				'padding-left': l + 'px',
				'padding-right': a + 'px',
			} );
			if ( r.hasClass( 'gutenify-container-full-width-stretch' ) ) {
				wrapperDiv.css( {
					'padding-left': 0,
					'padding-right': 0,
				} );
			}
			$( this ).trigger( 'containerStretched' );
		} );
	};

	$( window ).on( 'resize load', () => {
		stretchContainer();
	} );
	// stretchContainer();

	// FAQs
	$( document ).on( 'click', '.gutenify-content-toggle-item-wrapper .gutenify-content-toggle-item-header', function() {
		if ( $( this ).parent().parent().parent().hasClass( 'gutenify-block-faq-accordion' ) ) {
			$( this ).parent().parent().siblings().children().removeClass( 'gutenify-content-toggle-item-open' ).addClass( 'gutenify-content-toggle-item-closed' );
		}
		if ( $( this ).parent().hasClass( 'gutenify-content-toggle-item-open' ) ) {
			$( this ).parent().removeClass( 'gutenify-content-toggle-item-open' );
			$( this ).parent().addClass( 'gutenify-content-toggle-item-closed' );
		} else {
			$( this ).parent().addClass( 'gutenify-content-toggle-item-open' );
			$( this ).parent().removeClass( 'gutenify-content-toggle-item-closed' );
		}
	} );

	window.addEventListener( 'scroll', function() {
		if ( isOnScreen( $( '.gutenify--invisible' ) ) ) { /* Pass element id/class you want to check */
			$( '.gutenify--invisible' ).removeClass( 'gutenify--invisible' );
		}
	} );
} );
